@import "https://fonts.googleapis.com/css?family=Inter:100,200,300,regular,500,600,700,800,900";
html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, article, aside, footer, header, menu, nav, section {
  border: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

* {
  margin: 0;
  padding: 0;
}

h1, h2 {
  letter-spacing: -.05em;
  text-transform: uppercase;
  font-family: Inter, sans-serif;
  font-weight: 400;
}

h3 {
  letter-spacing: .02em;
  font-family: Inter, sans-serif;
  font-weight: 500;
}

p {
  letter-spacing: -.01em;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 150%;
}

.page {
  color: #1c1a26;
  scroll-behavior: smooth;
  font-family: Inter, sans-serif;
  font-weight: 400;
}

.page__body {
  background-color: #ffeefc;
  min-width: 320px;
  transition: background-color 3s;
}

.page__body .page__body.lightblue {
  background-color: #eef7ff;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0%);
}

.page__footer {
  background-color: #1c1a26;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.container {
  padding-inline: 16px;
}

@media (width >= 640px) {
  .container {
    padding-inline: 44px;
  }
}

@media (width >= 1024px) {
  .container {
    padding-inline: 44px;
  }
}

@media (width >= 1201px) {
  .container {
    max-width: 1200px;
    margin-inline: auto;
    padding-inline: 124px;
  }
}

.header {
  margin-top: 64px;
}

@media (width >= 1024px) {
