}

.header__nav {
  display: none;
}

@media (width >= 1024px) {
  .header__nav {
    display: block;
  }

  .header__nav-list {
    flex-direction: row;
    gap: 64px;
    display: flex;
  }

  .header__nav-link {
    color: #1c1a26;
    line-height: 24px;
  }

  .header__nav-link-call {
    letter-spacing: .02em;
    text-transform: uppercase;
    color: #f9512e;
    justify-content: center;
    align-items: center;
    gap: 4px;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    text-decoration: none;
    display: flex;
  }
}

.header__content {
  --columns: 2;
