.header__button {
  box-sizing: border-box;
  margin-inline: auto;
  border: 2px solid #f9512e;
  border-radius: 32px;
  grid-area: 4 / 1 / auto / -1;
  justify-content: center;
  align-items: center;
  gap: 1px;
  width: 290px;
  margin-bottom: 104px;
  padding: 16px 77px;
  text-decoration: none;
  transition: background-color .3s;
  display: flex;
}

.header__button:hover {
  background-color: #f9512e;
}

.header__button path {
  transition: fill .3s;
}

.header__button:hover path {
  fill: #fff;
}

.header__button-text {
  letter-spacing: .02em;
  text-transform: uppercase;
  color: #f9512e;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  transition: color .3s ease-in-out;
}

.header__button:hover .header__button-text {
  color: #fff;
}

.header__button:focus {
  background-color: #b2341a;
  border-color: #b2341a;
}

.header__button:focus path {
  fill: #fff;
}

.header__button:focus .header__button-text {
  color: #fff;
}

.header__button.disabled {
  cursor: not-allowed;
  background-color: #ffeefc;
  border: 2px solid #fba28f;
}

.header__button.disabled .header__button-text {
  color: #fba28f;
}

.header__button.disabled path {
  fill: #fba28f;
}

@media (width >= 640px) {
  .header__button {
    grid-area: 3 / 3 / auto / 7;
    width: 326px;
    margin-bottom: 228px;
  }
}

@media (width >= 1024px) {
  .header__button {
    grid-area: 1 / 1 / auto / 3;
    width: 260px;
    height: 56px;
    margin-top: 395px;
    margin-bottom: 161px;
    padding: 16px 62px;
  }
}

@media (width >= 1201px) {
  .header__button {
    margin-bottom: 138px;
  }
}

@keyframes float {
  0% {
    filter: drop-shadow(0 0 10px #f9512e);
    transform: translateY(0)scale(1)rotate(0);
  }

  25% {
    filter: drop-shadow(0 0 15px #f9512e);
    transform: translateY(-10px)scale(1.05)rotate(5deg);
  }

  50% {
    filter: drop-shadow(0 0 20px #f9512e);
    transform: translateY(0)scale(1.1)rotate(-5deg);
  }

  75% {
    filter: drop-shadow(0 0 15px #f9512e);
    transform: translateY(10px)scale(1.05)rotate(3deg);
  }

  100% {
    filter: drop-shadow(0 0 10px #f9512e);
    transform: translateY(0)scale(1)rotate(0);
  }
}

