    height: 30px;
  }
}

.nav__list {
  list-style: none;
}

.nav__link {
  letter-spacing: -.01em;
  padding-bottom: 2px;
  font-weight: 400;
  text-decoration: none;
  transition: color .3s;
  position: relative;
}

.nav__link:hover {
  color: #f9512e;
}

.nav__link:after {
  content: "";
  transform-origin: 100% 100%;
  background-color: #1c1a26;
  width: 100%;
  height: 2px;
  transition: transform .25s ease-out;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.nav__link:hover:after {
  transform-origin: 0 100%;
  transform: scaleX(1);
}

.logo {
  animation: 1.5s ease-in-out forwards logo-scale-up;
  display: block;
}

.logo svg {
  width: 100%;
  height: auto;
}

@keyframes draw-path {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes change-color-top-bar {
  0% {
    fill: #1c1a26;
  }

  50% {
    fill: #f9512e;
  }

  100% {
    fill: #b2341a;
  }
}

@keyframes change-color-footer {
  0% {
    fill: #fff;
  }

  50% {
    fill: #f9512e;
  }

  100% {
    fill: #b2341a;
  }
}

@keyframes logo-scale-up {
  0% {
    opacity: 0;
    transform: scale(.5);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.icon {
  cursor: pointer;
  display: inline-block;
  position: relative;
}

@media (width >= 640px) {
