}

.top-bar__logo path {
  stroke-dasharray: 500;
  stroke-dashoffset: 500px;
  animation: 2s forwards draw-path, 5s infinite alternate change-color-top-bar;
}

.top-bar-header {
  z-index: 1;
  background-color: #0000;
