  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.top-bar-header.scrolled {
  opacity: .9;
  background-color: pink;
  box-shadow: 0 2px 5px #0003;
}

:root {
  --glow-hue: 222deg;
  --shadow-hue: 180deg;
  --spring-easing: linear(0, 0, .01 0.9%, .04 1.8%, .16, .31 5.8%, .79 11.1%, 1.01 14.2%, 1.1, 1.16, 1.2, 1.22 20.3%, 1.23, 1.23, 1.23, 1.21 24.6%, 1.18 26.9%, 1.06 32.6%, 1.01 35.5%, .98, .97, .96, .95 42%, .95 44.1%, .95 46.5%, 1 57.2%, 1.01, 1.01 63.3%, 1.01 68.3%, 1 84%, 1);
  --spring-duration: 1.33s;
}

