.products__card-order {
  letter-spacing: .02em;
  text-transform: uppercase;
  color: #f9512e;
  isolation: isolate;
  box-shadow: 0 2px 3px 1px hsl(var(--glow-hue), 50%, 20%, 50%), inset 0 -10px 20px -10px hsla(var(--shadow-hue), 10%, 90%, 95%);
  transition: all var(--spring-duration) var(--spring-easing);
  background-image: linear-gradient(315deg, #ffc4ec -10%, #efdbfd 50%, #ffedd6 110%);
  border-radius: .66em;
  align-items: center;
  gap: 4px;
  padding: .8em 1.4em;
  font-weight: 500;
  line-height: 16px;
  text-decoration: none;
  display: flex;
  position: relative;
  scale: 1;
}

.products__card-order:active {
  transition-duration: calc(var(--spring-duration) * .5);
  scale: 1.04;
}

.products__card-order:hover:not(:active), .products__card-order.active {
  transition-duration: calc(var(--spring-duration) * .5);
  box-shadow: 0 4px 8px -2px hsl(var(--glow-hue), 50%, 20%, 50%), inset 0 0 0 transparent;
  scale: 1.04;
}

